import { CProgress, CProgressBar } from '@coreui/vue';
export default {
  name: 'ProgressCell',
  components: {
    CProgress,
    CProgressBar
  },
  props: {
    params: Object
  },
  computed: {
    progress() {
      return this.params && this.params.value != null ? this.params.value : 0;
    },
    barColor() {
      const val = this.progress;
      if (val < 30) return 'danger';
      if (val < 60) return 'warning';
      if (val < 100) return 'info';
      return 'success';
    }
  }
};